<template>
  <div id="app">
    <transition name="fade">
      <div class="layer" v-show="loaded">
        Mutsune
      </div>
    </transition>
    <div class="header">
      <p class="title">Simple Markdown Editor</p>
      <p class="subtitle">Vue + Marked for Speed Purpose</p>
    </div>

    <button @click="toggleMode">
      Toggle Invert Mode
    </button>
    <div class="editor">
      <div class="editor-child input">
        <textarea v-model="plain" placeholder="# Edit Here"></textarea>
      </div>
      <div class="editor-child preview" v-html="markdown">{{ markdown }}</div>
    </div>
  </div>
</template>

<script>
export default {
  created () {
    this.plain = `# Edit Here`
    this.markdown = this.$marked(this.plain, { sanitize: true })
    this.loaded = true
  },
  mounted () {
    this.$nextTick(() => {
      this.loaded = false
    })
  },
  data: () => {
    return {
      markdown: '',
      plain: '',
      loaded: false
    }
  },
  watch: {
    plain: function (event) {
      this.markdown = this.$marked(this.plain,
        { sanitize: true })
    }
  },
  methods: {
    toggleMode: function (event) {
      document.querySelector('*').classList.toggle('invert')
    }
  }
}
</script>

